module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">href</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Sets the href. When set will render the component as an anchor tag.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">icon</td><td style=\"padding-bottom: 1.8rem\">element</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">An optional icon. Nested inline with the text when provided.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isIconOnly</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Whether or not the button should only display as an icon.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isBlock</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Whether or not the button should display as a block.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isCompact</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Whether or not the button has reduced padding</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isDisabled</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Whether or not the button should be disabled.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isReversed</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Reverses the position of the icon and text.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onMouseDown</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when mouse is pressed.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onClick</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when clicked.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onBlur</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when button loses focus.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onFocus</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when button gains focus.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onKeyDown</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when key is pressed.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onKeyUp</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when key is released.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">refCallback</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Callback ref to pass into the dom element.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">text</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"color: #d53700; padding-bottom: 1.8rem\">required</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">none</td><td style=\"padding-bottom: 1.8rem\">Sets the button text.\nIf the button is <code style=\"white-space: pre-wrap\">isIconOnly</code> or variant <code>utility</code> this text is set as the aria-label and title for accessibility.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">title</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Additional information to display as a native tooltip on hover.\nButtons declared as <code style=\"white-space: pre-wrap\">isIconOnly</code> or <code>utility</code> will fallback to using <code>text</code> if not provided.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">type</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'button'</td><td style=\"padding-bottom: 1.8rem\">Sets the button type. One of <code style=\"white-space: pre-wrap\">button</code>, <code>submit</code>, or <code>reset</code>.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">variant</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'neutral'</td><td style=\"padding-bottom: 1.8rem\">Sets the button variant. One of <code style=\"white-space: pre-wrap\">neutral</code>,  <code>emphasis</code>, <code>ghost</code>, <code>de-emphasis</code>, <code>action</code> or <code>utility</code>.</td></tr></tbody></table>"